.page-feature {
	border-top: 1px solid darken($white,10%);
	border-bottom: 1px solid darken($white,10%);
	.feature-list {
		border-left: 1px solid darken($white,10%);
		.feature {
			border-right: 1px solid darken($white,10%);
			figure {
				padding: 20px 15px;
				.feature-img {
					@include flexbox;
					justify-content: center;
					align-items: center;
					align-content: center;
					height: 90px;
					a {
						+ a {
							margin-left: 5px; } }
					img {
						max-width: 100%;
						max-height: 100%; } }
				figcaption {
					text-align: center;
					.feature-name {
						@include fs(16rem);
						font-weight: 500;
						margin-bottom: 10px; }
					.feature-des {
						@include fs(13rem);
						color: darken($white,40%); } } } } } }

footer {
	.footer-top {
		padding: 40px 0;
		background: darken($white,4%);
		.footer-col {
			.company, .title {
				@include fs(15rem);
				font-weight: 700;
				margin-bottom: 15px; }
			.contact-info {
				@include fs(13rem);
				line-height: 1.8;
				color: darken($white,40%);
				a {
					color: darken($white,40%);
					&:hover {
						color: $blue; } } }
			.footer-link {
				a {
					display: block;
					@include fs(13rem);
					line-height: 1.8;
					@include fs(13rem);
					color: darken($white,40%);
					&:hover {
						color: $blue; } } }
			.subscribe-wrap {
				margin-bottom: 20px;
				.module-title {
					@include fs(13rem);
					line-height: 1.8;
					color: darken($white,40%);
					margin-bottom: 10px; }
				.subscribe {
					.subcribefrm {
						@include bor-rad(50px);
						overflow: hidden;
						@include flexbox;
						background: $white;
						padding: 1px;
						input {
							width: calc(100% - 38px);
							@include fs(13rem);
							line-height: 38px;
							color: darken($white,40%);
							background: none;
							border: none;
							margin: 0;
							padding: 0 20px; }
						.subcribebutton {
							width: 38px;
							height: 38px;
							line-height: 38px;
							text-align: center;
							color: $white;
							background: $orange;
							@include bor-rad(50px);
							@include fs(0rem);
							&:before {
								content: '\f061';
								@include fs(14rem); } } } } }
			.social {
				@include flexbox;
				margin-bottom: 20px;
				a {
					display: block;
					width: 32px;
					height: 32px;
					line-height: 32px;
					text-align: center;
					@include bor-rad(50px);
					background: $white;
					@include fs(16rem);
					color: darken($white,40%);
					+ a {
						margin-left: 10px; }
					&:hover {
						background: $blue;
						color: $white; } } } } }
	.footer-bottom {
		background: $blue;
		padding: 25px 0;
		.col-left,.col-right {
			padding: 0px 15px;
			flex: auto; }
		.sitemap {
			a {
				@include fs(12rem);
				color: color-rgba($white,0.8);
				+ a {
					&:before {
						content: '|';
						margin: 0px 5px; } }
				&:hover {
					color: $white; } } }
		.copyright {
			@include fs(12rem);
			color: color-rgba($white,0.8); }
		.payment-method {
			height: 100%;
			@include flexbox;
			justify-content: flex-end;
			align-items: center;
			align-content: center;
			label {
				@include fs(12rem);
				color: color-rgba($white,0.8);
				margin-right: 10px; } } } }
