.breadcrumb-wrap {
	ol.breadcrumb {
		margin: 0;
		padding: 20px 0;
		background: none;
		@include bor-rad(0);
		li {
			display: inline-block;
			color: darken($white,40%);
			+ li {
				&:before {
					content: '\f105';
					margin: 0px 5px; } }
			&:last-child {
				a {
					color: $main; } }
			a {
				@include fs(13rem);
				color: darken($white,40%);
				&:hover {
					color: $blue; } } } }
	.breadcrumb-img {
		@include pos(rel,null); }
	.pagename {
		@include fs(36rem);
		text-transform: uppercase;
		font-weight: 500;
		color: $white;
		@include pos(abs, 0 null 0 0);
		right: 0;
		@include flexbox;
		justify-content: center;
		align-items: center;
		align-content: center; } }
.breadcrumb-wrap2 {
	@include pos(rel,null);
	.breadcrumb-img {}
	.breadcrumb-content {
		@include pos(abs,60px 0 null 0);
		text-align: center; }
	ol.breadcrumb {
		margin: 0 0 15px;
		padding: 0;
		background: none;
		@include bor-rad(0);
		text-align: center;
		li {
			display: inline-block;
			color: darken($white,40%);
			+ li {
				&:before {
					content: '\f105';
					margin: 0px 5px; } }
			a {
				@include fs(14rem);
				font-weight: 600;
				color: $white;
				text-transform: uppercase;
				&:hover {
					color: $blue; } } } }
	.pagename {
		@include fs(48rem);
		// text-transform: uppercase
		font-weight: 100;
		color: $white; } }
