.bannerads {
	margin-bottom: 20px;
	overflow: hidden;
	@include media-breakpoint-down(sm) {
		margin-bottom: 10px; }
	.slick-prev,.slick-next {
		@include pos(abs,50% null null null);
		@include transform(translateY(-50%));
		@include fs(0);
		z-index: 5;
		color: $white;
		opacity: 0;
		&:before {
			content: '';
			@include fs(32rem); } }
	&:hover {
		.slick-prev,.slick-next {
			opacity: 1; } }
	.slick-prev {
		left: 15px;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: 15px;
		&:before {
			content: '\f105'; } }
	.slick-dots {
		@include pos(abs, null 30px 10px null);
		opacity: 0;
		li {
			display: inline-block;
			width: 10px;
			height: 10px;
			@include bor-rad(50px);
			background: $white;
			cursor: pointer;
			@include transition(all,0.5s);
			&.slick-active {
				background: $red; }
			+ li {
				margin-left: 8px; }
			button {
				display: none; } } } }

.category-info {
	@include flexbox;
	justify-content: space-between;
	.cate-name {
		@include fs(18rem);
		line-height: 30px;
		font-weight: 700;
		color: $blue;
		text-transform: uppercase;
		padding: 10px 0; }
	.col-right {
		@include flexbox;
		align-items: center;
		align-content: center;
		label {
			@include fs(13rem);
			font-weight: 500;
			color: darken($white,40%);
			margin-right: 5px; }
		.change-view {
			@include flexbox;
			align-items: center;
			align-content: center;
			padding-right: 20px;
			.btn-changeview {
				@include flexbox;
				a {
					@include fs(20rem);
					color: darken($white,40%);
					margin-left: 10px;
					&:hover {
						color: $orange; }
					&.active {
						color: $main; } } } }
		.product-sort {
			@include flexbox;
			align-items: center;
			align-content: center;
			padding-left: 20px;
			border-left: 1px solid darken($white,10%);
			select {
				appearance: none;
				-webkit-appearance: none;
				-moz-appearance: none;
				@include fs(14rem);
				font-weight: 600;
				border: none;
				color: $main;
				margin-left: 10px;
				cursor: pointer;
				&:hover {
					color: $orange; } } } } }
.product-list {
	.product-col {
		height: 100%;
		background: $white;
		figure {
			padding: 10px;
			@include pos(rel,null);
			.promotion {
				@include pos(abs, 10px null null 10px);
				@include fs(11rem);
				font-weight: 500;
				color: $white;
				padding: 2px 5px;
				text-transform: uppercase;
				// +bor-rad(5px)
				&.sale {
					background: $orange; }
				&.new {
					background: $blue; }
				&.hot {
					background: $amber; } }
			.product-img {
				@include flexbox;
				justify-content: center;
				align-items: center;
				height: 180px;
				img {
					max-width: 90%;
					max-height: 90%; } }
			figcaption {
				text-align: center;
				padding: 0px 40px;
				.product-name {
					@include fs(15rem);
					line-height: 1.4;
					font-weight: 600;
					text-transform: uppercase;
					margin-bottom: 5px;
					display: -webkit-box;
					height: 0.9375rem*1.4*2;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					a {
						color: $main; } }
				.product-price {
					.new {
						@include fs(17rem);
						color: $blue;
						font-weight: 600;
						line-height: 1.3; }
					.old {
						@include fs(15rem);
						text-decoration: line-through;
						color: darken($white,40%);
						font-weight: 500;
						line-height: 1.3; } }
				.btn-wrap {
					@include pos(abs,0 null 0 0);
					right: 0;
					@include flexbox;
					justify-content: center;
					align-items: center;
					align-content: center;
					z-index: 5;
					visibility: hidden;
					opacity: 0;
					@include transition(all,0.5s);
					.btn-buy {
						display: block;
						line-height: 40px;
						color: $white;
						background: $blue;
						padding: 0px 15px;
						@include bor-rad(50px 0 0 50px);
						@include fs(20rem);
						box-shadow: 0px 10px 20px -10px color-rgba($black,0.5);
						&:hover {
							background: $orange; } }
					.btn-viewdetail {
						display: block;
						line-height: 40px;
						color: $white;
						background: $blue;
						white-space: nowrap;
						border-left: 1px solid $white;
						padding: 0px 20px;
						@include bor-rad(0px 50px 50px 0px);
						@include fs(13rem);
						font-weight: 600;
						text-transform: uppercase;
						box-shadow: 0px 10px 20px -10px color-rgba($black,0.5);
						&:hover {
							background: $orange; } } } }
			&:hover {
				.btn-wrap {
					visibility: visible;
					opacity: 1; } } } } }
.product-page {
	.product-list {
		border-top: 1px solid darken($white,10%);
		border-left: 1px solid darken($white,10%);
		.row {
			margin: 0px 0px; }
		.col-6 {
			padding: 0px 0px; }
		.product-col {
			border-right: 1px solid darken($white,10%);
			border-bottom: 1px solid darken($white,10%); } }
	.product-detail {
		padding-top: 50px; }
	.slide-wrap {
		@include flexbox;
		margin-bottom: 30px;
		.product-slide {
			width: calc(100% - 82px);
			.item {
				.fancybox {
					@include flexbox;
					width: 100%;
					height: 500px;
					justify-content: center;
					align-items: center;
					img {
						max-width: 100%;
						max-height: 100%; } } } }
		.nav-wrap {
			width: 82px;
			.product-nav {
				width: 82px;
				.slick-prev, .slick-next {
					@include fs(0);
					display: block;
					width: 100%;
					text-align: center;
					line-height: 1;
					color: darken($white,40%);
					@include media-breakpoint-down(xs) {
						@include pos(abs, 50% null null null);
						@include transform(translateY(-50%));
						z-index: 5;
						width: unset; }
					&:hover {
						color: $main; }
					&:before {
						content: '';
						@include fs(24rem); } }
				.slick-prev {
					@include media-breakpoint-down(xs) {
						left: 0; }
					&:before {
						content: '\f106';
						@include media-breakpoint-down(xs) {
							content: '\f104'; } } }
				.slick-next {
					@include media-breakpoint-down(xs) {
						right: 0; }
					&:before {
						content: '\f107';
						@include media-breakpoint-down(xs) {
							content: '\f105'; } } }
				.item {
					padding: 5px 0;
					.img {
						width: 80px;
						height: 80px;
						@include flexbox;
						justify-content: center;
						align-items: center;
						border: 1px solid darken($white,10%);
						cursor: pointer;
						overflow: hidden;
						@include transition(all,0.5s);
						img {
							max-width: 80%;
							width: 80%; } }
					&.slick-current {
						.img {
							border-color: $blue; } } } }
			.btn-video {
				display: block;
				@include pos(rel,null);
				img {
					width: 100%; }
				&:before {
					content: '\f03d';
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: color-rgba($black,0.5);
					@include flexbox;
					justify-content: center;
					align-items: center;
					@include fs(24rem);
					color: $white; } } } }
	.product-info {
		@include pos(rel,null);
		margin-bottom: 30px;
		@include media-breakpoint-down(md) {
			margin-bottom: 10px; }
		.promotion {
			@include pos(abs, 3px null null -30px);
			@include transform(translateX(-100%));
			color: $white;
			z-index: 5;
			// +bor-rad(3px)
			// font-weight: 700
			padding: 3px 10px;
			@include fs(13rem);
			text-transform: uppercase;
			@include media-breakpoint-down(md) {
				@include pos(static,unset);
				display: inline-block;
				@include transform(translateX(0));
				margin-bottom: 5px;
				@include fs(12rem);
				padding: 2px 8px; }
			&.new {
				background: $blue; }
			&.sale {
				background: $red; } }
		.product-name {
			@include fs(24rem);
			text-transform: uppercase;
			font-weight: 600;
			color: $main;
			line-height: 1.3;
			margin-bottom: 5px;
			@include media-breakpoint-down(lg) {
				@include fs(22rem);
				margin-bottom: 0; } }
		.product-category {
			@include fs(14rem);
			color: darken($white,40%); }
		.product-id {
			@include fs(14rem);
			color: darken($white,40%);
			margin-bottom: 15px;
			a {
				color: $main; } }
		.product-overview {
			border: 1px solid darken($white,10%);
			padding: 20px;
			color: darken($white,60%);
			margin-bottom: 20px;
			ul {
				@include flexbox;
				flex-wrap: wrap;
				li {
					@include pos(rel,null);
					@include fs(14rem);
					line-height: 1.8; } } }
		.product-price {
			@include flexbox;
			margin-bottom: 15px;
			.new {
				@include fs(24rem);
				font-weight: 600;
				color: $black;
				margin-right: 10px;
				@include media-breakpoint-down(lg) {
					@include fs(22rem); } }
			.old {
				@include fs(18rem);
				text-decoration: line-through;
				color: darken($white,20%);
				margin-top: 7px;
				@include media-breakpoint-down(lg) {
					@include fs(16rem);
					margin-top: 6px; } } }
		.product-color, .product-size {
			width: 100%;
