			@include media-breakpoint-down(md) {
				width: 50%; }
			@include media-breakpoint-down(xs) {
				width: 100%;
				margin-bottom: 0; }
			.label {
				@include fs(12rem);
				font-weight: 500;
				color: $main;
				text-transform: uppercase;
