				a {
					display: block;
					width: 28px;
					height: 28px;
					@include bor-rad(50px);
					// opacity: 0.5
					border: 2px solid darken($white,20%);
					@include pos(rel,null);
					&:before {
						content: '';
						display: block;
						border: 3px solid $white;
						@include bor-rad(50px);
						@include pos(abs, 0 null 0 0);
						right: 0; }
					&:hover {
						border-color: darken($white,40%); }
					&.disable {
						opacity: 0.2;
						border-color: darken($white,20%); }
					&.active {
						opacity: 1;
						border-color: $black; } } } }
		.input-number {
			@include bor-rad(50px);
			overflow: hidden;
			.btn-spin {
				width: 40px;
				height: 30px;
				line-height: 30px; }
			input {
				width: 40px;
				padding: 0px;
				line-height: 30px;
				border-left: 1px solid darken($white,10%);
				border-right: 1px solid darken($white,10%); } }
		.btn-wrap {
			@include flexbox;
			align-items: center;
			align-content: center;
			margin-bottom: 20px;
			label {
				@include fs(13rem);
				color: darken($white,60%);
				font-weight: 500;
				margin-right: 20px; }
			.btn-buy {
				display: block;
				padding: 0px 30px;
				@include bor-rad(50px);
				line-height: 40px;
				@include fs(15rem);
				text-transform: uppercase;
				font-weight: 600;
				color: $white;
				background: $blue;
				margin-left: 20px;
				overflow: hidden;
				&:before {
					background: color-rgba($white,0.1);
					@include bor-rad(50px); }
				&:hover {} }

			.btn-wishlist {
				display: inline-block;
				width: 40px;
				height: 40px;
				line-height: 40px;
				text-align: center;
				color: $black;
				background: none;
				border: 1px solid $black;
				// +bor-rad(50px)
				margin-left: 10px;
				@include fs(18rem);
				@include media-breakpoint-down(md) {
					margin-left: 5px; }
				@include media-breakpoint-down(sm) {
					display: block; }
				&:hover {
					border-color: lighten($red,20%);
					background: lighten($red,20%);
					color: $white; }
				&.active {
					border-color: lighten($red,20%);
					background: lighten($red,20%);
					color: $white; } }
			.buy-support {
				@include fs(14rem);
				display: block;
				color: darken($white,60%);
				padding: 10px 0;
				@include media-breakpoint-down(sm) {
					display: none; }
				&:after {
					content: '\f105';
					margin-left: 5px; }
				&:hover {
					color: $red; } } }
		.share {
			@include flexbox;
			align-items: center;
			align-content: center;
			label {
				@include fs(13rem);
				color: darken($white,60%);
				font-weight: 500;
				margin-right: 20px; }
			a {
				display: block;
				width: 32px;
				height: 32px;
				line-height: 32px;
				text-align: center;
				@include bor-rad(50px);
				@include fs(16rem);
				// color: darken($white,40%)
				background: darken($white,8%);
				+ a {
					margin-left: 5px; }
				&:hover {
 } }					// color: $blue
			@include media-breakpoint-down(lg) {
				margin-bottom: 15px; } } }
