@keyframes hotline-animate {
	0% {
		@include transform(scale3d(1.3,1.3,1.3));
		opacity: 0.5; }
	100% {
		@include transform(scale3d(2.2,2.2,2.2));
		opacity: 0; } }
@keyframes phone-ring {
	0% {
		transform: rotate(0deg) scale(1) skew(1deg); }
	5% {
		transform: rotate(-25deg) scale(1) skew(1deg); }
	10% {
		transform: rotate(25deg) scale(1) skew(1deg); }
	15% {
		transform: rotate(-25deg) scale(1) skew(1deg); }
	20% {
		transform: rotate(25deg) scale(1) skew(1deg); }
	25% {
		transform: rotate(0deg) scale(1) skew(1deg); }
	100% {
		transform: rotate(0deg) scale(1) skew(1deg); } }
.phonecall {
	@include pos(fix, null 20px 20px null);
	// +transform(translate(-50%,0))
	z-index: 50;
	&:before {
		content: '\f095';
		font-family: 'fontawesome';
		@include fs(28rem);
		display: block;
		font-weight: normal;
		width: 50px;
		height: 50px;
		line-height: 50px;
		text-align: center;
		// background: linear-gradient(135deg,$blue,$menu)
		background: $orange;
		// opacity: 0.9
		color: color-rgba($white,1);
		@include bor-rad(50px);
		text-shadow: none;
		// box-shadow: 0px 10px 20px -10px color-rgba($black,0.3)
		@include pos(rel,null);
		z-index: 1;
		animation-name: phone-ring;
		animation-duration: 2s;
		animation-iteration-count: infinite;
		@include transition(all,0.5s); }
	&:after {
		content: '';
		display: block;
		@include pos(abs, 0 null 0 0);
		right: 0;
		background: $orange;
		opacity: 0.5;
		@include bor-rad(50px);
		@include transform(scale3d(1.2,1.2,1));
		animation-name: hotline-animate;
		animation-duration: 1s;
		animation-iteration-count: infinite;
		@include transition(all,0.5s); }
	&:hover {
		&:before, &:after {
			background: lighten($orange,10%); } } }
